.gpt3__possibility {
    display: flex;
    flex-direction: row;
}

.gpt3__possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-right: 2rem;
}

.gpt3__possibility-image img {
    width: 80%;
    height: 100%;
}

.gpt3__possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.gpt3__possibility-content h4 {
font-family: var(--font-family);
font-weight: 500;
font-size: 16px;
line-height: 30px;
color: #E6CAFC;
}

.gpt3__possibility-content h4:last-child {
    color: #FF8A71;

}

.gpt3__possibility-content h1 {
font-family: var(--font-family);
font-weight: 800;
font-size: 32px;
line-height: 45px;
color: #FF8A71;

margin: 1rem 0;
}

.gpt3__possibility-content p {
font-family: var(--font-family);
font-weight: 400;
font-size: 16px;
line-height: 30px;
color: #fff;

margin-bottom: 2rem;
}

@media screen and (max-width: 950px) {
    .gpt3__possibility {
        flex-direction: column;
    }

    .gpt3__possibility-image {
        margin: 1rem 0;
    }

    .gpt3__possibility-content {
        margin-top: 2rem;
    }
}